<template>
  <div class="container">
    <h2 class="text-center">個人專區</h2>
    <button class="btn btn-outline-secondary btn-sm mb-3" type="button" @click="$router.push('/examLobby')">返回大廳</button>
    <!-- 訊息提示框 -->
    <Alert
      :MODE="MSG.MODE"
      :MSGTYPE="MSG.TYPE"
      :TITLE="MSG.TITLE"
      :TEXT="MSG.TEXT"
      @alert-check="(isClick, OKorCancel) => (alertCheck = { isClick, OKorCancel })"
    />
    <div class="row">
      <div class="col-12">
        <div class="form-floating mb-3">
          <input type="text" class="form-control" name="studentIDInput" v-model="user.studentID" placeholder="學號" disabled />
          <label for="studentIDInput">學號</label>
        </div>
        <div class="form-floating mb-3">
          <input type="text" class="form-control" name="accountInput" v-model="user.account" placeholder="帳號" disabled />
          <label for="accountInput">帳號</label>
        </div>
        <div class="form-floating mb-3">
          <input type="text" class="form-control" name="userNameInput" v-model="user.name" placeholder="名稱" />
          <label for="userNameInput">名稱</label>
        </div>
      </div>
      <div class="col-12 d-grid gap-2 my-2">
        <button class="btn btn-success btn-lg" type="button" @click="sendEdit()">確認修改</button>
      </div>
    </div>
    <div class="col-12 d-grid gap-2 my-2">
      <button class="btn btn-warning btn-lg" type="button" @click="resetExamExp()" disabled>重置學習紀錄(7筆)(開發中)</button>
    </div>
    <div class="col-12">
      <table class="table table-success table-striped">
        <tr>
          <td colspan="4" class="h2 text-center">登入紀錄(開發中)</td>
        </tr>
        <tr>
          <td class="h4">時間</td>
          <td class="h4">裝置</td>
          <td class="h4">IP位置</td>
        </tr>
        <tr>
          <td class="fw-bolder">11308041742</td>
          <td class="text-center">Windows 10</td>
          <td class="text-center">172.12.31.22</td>
        </tr>
        <tr>
          <td class="fw-bolder">11308041815</td>
          <td class="text-center">iPhone SE</td>
          <td class="text-center">176.12.31.22</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import Alert from "@/components/Alert.vue";

export default {
  name: "Profile",
  components: {
    Alert,
  },
  data() {
    return {
      // 訊息提示框資料容器
      MSG: {
        MODE: "",
        TYPE: "",
        TITLE: "",
        TEXT: [],
      },
      // 訊息確認[開關]
      alertCheck: false,
      // 訊息功能碼
      funcNum: 0,
      user: {
        studentID: "",
        account: "",
        name: "",
        ident: "",
      },
    };
  },
  methods: {
    sendEdit() {
      this.alertVisible = true;
      this.MSG.MODE = "OKCancel";
      this.funcNum = 1;
      this.MSG.TYPE = "warning";
      this.MSG.TITLE = "系統警告";
      this.MSG.TEXT = ["是否要修改帳戶參數？"];
    },
    resetExamExp() {
      this.alertVisible = true;
      this.MSG.MODE = "OKCancel";
      this.funcNum = 2;
      this.MSG.TYPE = "warning";
      this.MSG.TITLE = "系統警告";
      this.MSG.TEXT = ["是否要重置學習紀錄？"];
    },
    hideAlert() {
      this.$store.commit("SET_ALERT_VISIBLE", false);
      this.alertVisible = false;
    },
  },
  created() {
    // 載入資料
    this.user = this.$store.state.account;
  },
  mounted() {},
  computed: {
    // alert開關連動狀態功能
    alertVisible: {
      get() {
        return this.$store.state.alertVisible;
      },
      set(value) {
        this.$store.commit("SET_ALERT_VISIBLE", value);
      },
    },
  },
  watch: {
    alertCheck: function (val) {
      if (val.isClick) {
        if (val.OKorCancel === "OK") {
          if (this.funcNum === 0) {
            return;
          } else if (this.funcNum === -1) {
            this.hideAlert();
          } else if (this.funcNum === -2) {
            this.hideAlert();
            this.$router.go(-1);
          } else if (this.funcNum === 1) {
            // 修改用戶參數
            this.hideAlert();
            const user = {
              studentID: this.user.studentID,
              name: this.user.name,
              account: this.user.account,
              ident: this.user.ident,
            };
            this.$http
              .post("api/users/modify", { studentID: this.$store.state.account.studentID, name: this.user.name })
              .then((res) => {
                if (res.data.code === 200) {
                  this.alertVisible = true;
                  this.MSG.MODE = "OKOnly";
                  this.funcNum = -2;
                  this.MSG.TYPE = "primary";
                  this.MSG.TITLE = "系統提示";
                  this.MSG.TEXT = [];
                  this.MSG.TEXT = this.MSG.TEXT.concat(res.data.msg);
                } else {
                  this.alertVisible = true;
                  this.MSG.MODE = "OKOnly";
                  this.funcNum = -1;
                  this.MSG.TYPE = "danger";
                  this.MSG.TITLE = "系統錯誤";
                  this.MSG.TEXT = [];
                  this.MSG.TEXT = this.MSG.TEXT.concat(res.data.msg);
                  if (res.data.sys !== "") this.MSG.TEXT.push(res.data.sys);
                }
              })
              .catch((err) => {
                this.alertVisible = true;
                this.MSG.MODE = "OKOnly";
                this.funcNum = -1;
                this.MSG.TYPE = "danger";
                this.MSG.TITLE = "系統錯誤";
                this.MSG.TEXT = [];
                this.MSG.TEXT.push(err);
              });
          } else if (this.funcNum === 2) {
            // 重置學習紀錄
            this.hideAlert();
            this.$http
              .post("api/accountManage/resetExamExp", { studentID: this.user.studentID })
              .then((res) => {
                if (res.data.code === 200) {
                  this.alertVisible = true;
                  this.MSG.MODE = "OKOnly";
                  this.funcNum = -1;
                  this.MSG.TYPE = "primary";
                  this.MSG.TITLE = "系統提示";
                  this.MSG.TEXT = [];
                  this.MSG.TEXT = this.MSG.TEXT.concat(res.data.msg);
                } else {
                  this.alertVisible = true;
                  this.MSG.MODE = "OKOnly";
                  this.funcNum = -1;
                  this.MSG.TYPE = "danger";
                  this.MSG.TITLE = "系統錯誤";
                  this.MSG.TEXT = [];
                  this.MSG.TEXT = this.MSG.TEXT.concat(res.data.msg);
                  if (res.data.sys !== "") this.MSG.TEXT.push(res.data.sys);
                }
              })
              .catch((err) => {
                this.alertVisible = true;
                this.MSG.MODE = "OKOnly";
                this.funcNum = -1;
                this.MSG.TYPE = "danger";
                this.MSG.TITLE = "系統錯誤";
                this.MSG.TEXT = [];
                this.MSG.TEXT.push(err);
              });
          }
        } else if (val.OKorCancel === "Cancel") {
          this.hideAlert();
        }
      }
    },
  },
};
</script>

<style lang="scss">
table {
  border: 5px double black;
  margin: auto;
  width: 100%;
}
td {
  border: 1px solid black;
}
</style>
